.navbar {
  display: flex;
  position: sticky;
  background-color: #fff;
  left: 0;
  top: 0;

  justify-content: space-between;
}

.logo {
  height: 40px;
  width: 200px;
  margin: 10px;
  border: 1px solid black;
}

.navmenu {
  display: flex;
}

.navmenu li {
  list-style-type: none;
  padding-left: 20px;
  padding-right: 20px;
  margin: 4px;
}

.navmenu li a {
  font-weight: 600;
  color: #000;
  text-decoration: none;
}
